import React, { useContext } from 'react';
import fetchData from './fetchData';
import { Store } from './Store';
import Card from '../../Components/Card';
import Pagination from '../../Components/Pagination';
import Tabs from '../../Components/Tabs';
import Loading from '../../App/Loading';
import type EpisodeProps from './Episode';

export default () => {
  const { state, dispatch } = useContext(Store),
    handleFavorite = (episode: EpisodeProps) => {
      return state.favorites.includes(episode)
        ? dispatch({
            payload: state.favorites.filter(
              (fav: EpisodeProps) => fav.id !== episode.id
            ),
            type: 'remove_favorite',
          })
        : dispatch({
            payload: episode,
            type: 'add_favorite',
          });
    },
    getCards = (episodes: [EpisodeProps]) =>
      episodes.map((
        {
          id,
          image: { medium },
          name,
          number,
          season,
          summary,
        }: EpisodeProps,
        key: number,
        callee
      ) => (
        <Card
        key={key}
        image={{
          alt: name,
          src: medium,
        }}
        col="l4 m6 s12"
        title={name}
        content={`
          Season: ${season} | 
          Episode: ${number}
       `}
        reveal={
          summary === null
            ? 'no description'
            : summary.replace(/<(.|\n)*?>/g, '')
        }
        fab={() => handleFavorite(callee[key])}
        fabIcon={state.favorites.includes(callee[key]) ? 'remove' : 'add'} />
      ));

  !state.episodes.length && fetchData();

  return state.episodes.length ? (
    <Tabs tabs={['episodes', 'favorites']}>
      <Pagination
      itemsPerPage={12}>
        {getCards(state.episodes)}
      </Pagination>
      <div className="row">
        {getCards(state.favorites)}
      </div>
    </Tabs>
  ) : (
    <Loading />
  );
};